import React, { Component } from 'react';
import { Link, NavLink} from 'react-router-dom';
import {Button, Col, Row} from 'react-materialize';


class Navbar extends Component {
  componentDidMount(){
    document.querySelector('.button-burger').addEventListener('click',()=>{
      if(document.querySelector('.menu-button').style.visibility === 'visible'){
        document.querySelectorAll('.menu-button')[0].style.visibility = 'hidden';
        document.querySelectorAll('.menu-button')[1].style.visibility = 'hidden';
      }
      else{
        document.querySelectorAll('.menu-button')[0].style.visibility = 'visible';
        document.querySelectorAll('.menu-button')[1].style.visibility = 'visible';
      }
    })
  }
  render() {
    return (
      <div className="container">
        <Row className='nav-button'>
          <Col s={12} ><Button className='red btn-large btn-floating button-burger' waves='green'><i className="fas fa-bars white-text fa-3x"></i></Button>
          <Link to='/'><Button className='blue btn-medium btn-floating menu-button' waves='red'><i className="fas fa-home white-text fa-2x"></i></Button></Link>
          <NavLink to='/about'><Button className='green btn-medium btn-floating menu-button' waves='red'><i className="fas fa-user white-text fa-2x"></i></Button></NavLink></Col>
        </Row>
      </div>
    );
  }
}

export default Navbar;
